.txt {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: xx-large;
    font-weight: bolder;
    font-size: xx-large;
    color: #715d5c;
}

$black: #222;

*,
*:before,
*:after {
    box-sizing: border-box;
}

html {
    font-size: auto;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    position: relative;
    background: #6298C8;
}

.gallery-container {
    padding: .9375rem 0;
}

.gallery-container h1 {
    margin: 2rem 0;
    padding: 0;
    text-align: center;
}

.gallery-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 8px;
    max-width: auto;
    width: 100%;
    margin: 0 auto;

    @include bp(x-small) {
        grid-template-columns: repeat(1, 1fr);
    }

    @include bp(small) {
        grid-template-columns: repeat(2, 1fr);
    }

    @include bp(large) {
        grid-template-columns: repeat(3, 1fr);
    }

    img {
        width: 100%;
        border: 5px solid #fff;
    }
}

.gallery-grid div {
    position: relative;
    cursor: pointer;

    &:before,
    &:after {
        transition: .3s opacity ease;
        opacity: 0;
    }

    &:after {
        content: '\02194';
        font-size: 80px;
        position: absolute;
        transform: translate3d(-50%, -50%, 0) rotate(-45deg);
        color: #fff;
        left: 50%;
        top: 50%;
        display: block;
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 4px;
        left: 0;
        right: 0;
        background: rgba(#222, 0.5);
        display: block;
    }

    &:hover {

        &:before,
        &:after {
            opacity: 1;
            transition: .3s opacity ease;
        }
    }
}

.modal1 {
    position: fixed;
    z-index: 999;
    width: 30%;
    max-width: 800px;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);

    @include bp(x-small) {
        width: 95%;
    }

    @include bp(small) {
        width: 80%;
    }

    @include bp(large) {
        width: 60%;
    }

    img {
        width: 100%;
        border: 5px solid #fff;
    }
}

.modal1-overlay {
    position: fixed;
    z-index: 1;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
}

.modal1-body a {
    position: absolute;
    display: inline;
    color: $black;
    text-decoration: none;
    line-height: 36px;
    font-size: 30px;
    font-weight: lighter;
    background: #fff;
    border-radius: 5px;
    text-align: center;
}

.modal1-body .modal1-close {
    right: 0;
    top: 0;
    border-radius: 0 0 0 5px;
}

.modal1-body .modal1-next,
.modal1-body .modal1-prev {
    right: 0;
    top: calc(50% - 25px);
    border-radius: 5px 0 0 5px;
    height: 50px;
    line-height: 40px;
    font-size: 60px;
}

.modal1-body .modal1-prev {
    left: 0;
    right: auto;
    border-radius: 0 5px 5px 0;
}

.modal1-body {
    position: relative;
}