.popup1 {
  position: fixed;
  width: 30%;
  height: 20%;
  top: 5%;
  left: 0;
  right: 40%;
  bottom: 0;
  margin: auto;
  border-radius: 20px;
  border-color: red;
  border-style: solid;
  background-color: white;
}

